<template>
  <div class="index-contin">
    <div class="index-swiper">
      <mt-swipe :auto="4000" :show-indicators="true">
        <mt-swipe-item v-for='item in subjectData'>
          <router-link :to='"/topic/?id="+ item.id'>
            <img :src="item.coverImg">
          </router-link>
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="index-search">
      <router-link to='/search'>
        <div class="search-box">
          <span>搜索你想要的课程</span>
        </div>
      </router-link>
      <div class="word-box">
        <keep-alive>
          <skillWord :wordArr='wordArr'></skillWord>
        </keep-alive>
      </div>
    </div>
    <div class="popular">
      <popular :postData='postData' :postType='postType' :title='title'></popular>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  //  import {mapGetters} from 'vuex';
  import popular from '../popular/popular.vue';
  import skillWord from '../skillWord/skillWord.vue';
  import API_lesson from '../../../src/api/LessonMain';
  import API_Subject from '../../../src/api/Subject';

  export default {
    data () {
      return {
        title:'热课榜',
        postType:'0',
        wordArr:[],
        subjectData:[]
      };
    },
    components: {
      popular,
      skillWord
    },
    computed:{
      postData:function () {
        return this.$store.state.index1.postData
      }
    },
    created () {
      let self=this;

//      初始化wordArr
      let obj = window.sessionStorage.getItem('wordArr');
      if(!obj){
        API_lesson.querySkillType({pageSize:-1}).then(function (res) {
          self.wordArr=res;
          let str=JSON.stringify(res);
          window.sessionStorage.setItem('wordArr',str);
        })
      }else {
        this.wordArr = JSON.parse(obj);
      }

//      获取专题信息
      API_Subject.queryRecommendedSubject({}).then(function (res) {
        self.subjectData=res;
      })

    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less">
  .index-contin {
    width: 750px;
    height: 100%;
    .index-swiper {
      box-sizing: border-box;
      width: 100%;
      height: 4.66*100px;
      padding-bottom: 0.4*100px;
      img {
        width: 100%;
      }
    }
    .index-search {
      width: 7.5*100px;
      color: gray;
      a{
        display: block;
        .search-box {
          width: 6.23*100px;
          height: .7*100px;
          margin: 0 auto;
          border: 0.01*100px solid gainsboro;
          position: relative;
          span {
            position: absolute;
            height: .24*100px;
            width: 300px;
            top: 50%;
            left: 50%;
            margin-top: -0.12*100px;
            margin-left: -1.15*100px;
            font-size: 24px;
            padding-left: .35*100px;
            background: url("../../../static/images/index1/search.png") no-repeat;
          }
        }
      }

      .word-box {
        width: 623px;
        margin: 0 auto;
      }
    }
    .popular-box {
      width: 100%;
    }
  }
</style>



