import API_lesson from '../../api/LessonMain';
// import * as types from '../mutation-types';
import { Indicator } from 'mint-ui';

//状态
const state = {
  topList0:[],
  topList1:[],
  postType:0,
  loading:false
};

// getters
// 理解为  state的计算属性
const getters = {
    listadd : function () {
      return  'topList'+state.postType;
    }
};

// actions
//异步的mutations
const actions = {
  upDateTopList (context,parms) {
    context.commit('_UPDATATOPLIST',parms);
  },
  empty(context) {
    context.commit('_EMPTY');
  }
};

// mutations
//改变state状态的函数
const mutations = {

  _UPDATATOPLIST (state,parms) {
    if(!state.loading){
      //控制触发
      state.loading=true;
      parms.postData.pageNumber++;
      // console.log('pageNumber为'+parms.postData.pageNumber);
      state.postType=parms.postType;
      console.log('postType为'+parms.postType);

      Indicator.open({
        text: '加载中...',
        spinnerType: 'double-bounce'
      });
      API_lesson.queryLessonByPage(parms.postData)
        .then(function (res) {
          let arr=state[getters.listadd()];
          state[getters.listadd()]= arr.concat(res);
          Indicator.close();
          state.loading=false;
          console.log(parms.postData)
        }, function (err) {
          Indicator.close();
          state.loading=false;
        });
    }

  },
  _EMPTY(state) {

    state.topList1=[];
  }
};

//暴露出去 store.js 整合
export default {
  state,
  getters,
  actions,
  mutations
}



