<template>
  <div class="classCation">
    <div class="newLesson">
      <p class="title">新课速递</p>
      <div class="lesson-box">
        <li v-for='(item, index) in newLesson'>
          <router-link :to='"/course/?id="+item[0].id'>
            <div class="imgbox">
              <img :src="item[0].coverImg">
            </div>
            <div class="smTitle">
                {{ item[0].title }}
            </div>
          </router-link>
        </li>
      </div>
    </div>
    <div class="classPopular">
      <popular :postData='postData' :postType='postType' :title='title'></popular>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">
  import popular from '../popular/popular.vue';
  import API_lesson from '../../../src/api/LessonMain';

  export default {
    data () {
      return {
        newLesson:[],
        title:'热课榜',
        skillInfoId:'',
        postType:1
      }
    },
    computed:{
      postData:function () {
        let prams={skillInfoId:this.skillInfoId,orderType:3,pageNumber:0};
        return prams;
      },
    },
    components:{
      popular
    },
    created() {
      if(this.postType==1){
        this.$store.dispatch('empty');
      }
      let self = this;
      self.skillInfoId = self.$route.query.skillInfoId;
      API_lesson.queryLessonByPage({skillInfoId:self.skillInfoId,pageSize:3,pageNumber:1}).then(function (res) {
      self.newLesson=res;
      })
    }
  }
</script>

<style lang="less" rel="stylesheet/less">
.classCation{
  width: 677px;
  margin: 0 auto;
  .newLesson{
    height: 450px;
    .title{
      height: 90px;
      padding: 30px 0;
      font-size: 32px;
      color: gray;
    }
    .lesson-box{
      display: flex;
      justify-content:space-between;
      li{
        width: 190px;
        height: 386px;
        flex: none;
        img{
          width: 100%;
          height: 250px;
        }
        .smTitle{

          font-size: 20px;
          padding-top: 15px;
        }
      }
    }
  }
}
</style>



