import http from './httputil';
const API_LessonMain = {

  queryLessonByPage: function (params) {
    let defUrl = '/lessonMain/queryLessonByPage';
    let defParams = {
      orderType: 3,
      pageNumber: 1,
      pageSize: 15
    };
    //合并参数
    let obj = Object.assign(defParams, params)

    return http.fetch(defUrl, obj)
  },
  queryLessonById:function (params) {

    let defUrl = '/lessonMain/queryLessonById';
    let defParams = {
      id:"823606885"
    };
    //合并参数
    let obj = Object.assign(defParams, params);
    return http.fetch(defUrl, obj)
  },
  queryLessonComment:function (params) {

  let defUrl = '/lessonComment/queryLessonComment';
  let defParams = {
    lessonId:"823606885"
  };
  //合并参数
  let obj = Object.assign(defParams, params);
  return http.fetch(defUrl, obj)
},
  // http://api.111ji.com/lessonMain/queryLessonByKey
  queryLessonByKey:function (params) {

    let defUrl = '/lessonMain/queryLessonByKey';
    let defParams = {
      key:"",
      orderType:3,
      pageNumber:1
    };
    //合并参数
    let obj = Object.assign(defParams, params);
    return http.fetch(defUrl, obj)
  },
  // http://api.111ji.com/skillType/querySkillInfo
  querySkillType:function (params) {

    let defUrl = 'skillType/querySkillType';
    let defParams = {
      // name:'',
      // parentId:'',
      // pageNumber:1
    };
    //合并参数
    let obj = Object.assign(defParams, params);
    return http.fetch(defUrl, obj)
  },
};

export default API_LessonMain ;



