<template>
  <div class="topic">
    <div class="head">
      <div class="img_box">
        <img :src="subjectData.subject.coverImg" alt="">
        <h1 class="title">
          {{ subjectData.subject.title }}
        </h1>
      </div>
      <p class="sub_content">
        {{ subjectData.subject.content }}
      </p>
      <h2 class="sm_title">

        {{ subjectData.subjectDetail[0].title }}
      </h2>
      <p class="sub_content">
        {{subjectData.subjectDetail[0].description}}
      </p>

      <ul>
        <li v-for='(item, index) in subjectData.lessons'>
          <router-link :to='"/course/?id="+item[0].id'>
            <div class="imgbox">
              <img v-lazy="item[0].coverImg">
            </div>
            <div class="left">
              <div class="title">
                {{ item[0].title }}
              </div>
              <div class="teacher">
                讲师：{{ item[1].nickName }}
              </div>
              <div class="attention">
                {{ item[0].likeNum }}人关注&nbsp/&nbsp{{ item[0].vipNum }}人购买
              </div>
              <div class="price">
                <span>￥{{ item[0].price }}&nbsp/&nbsp年</span>
              </div>
            </div>
            <span class="sourcetype">
          在行
        </span>
          </router-link>
        </li>
      </ul>

    </div>

  </div>
</template>

<script type="text/ecmascript-6">
  import API_Subject from '../../../src/api/Subject';

  export default {
    data() {
      return {
        subjectData:[]
      };
    },
    created: function() {
      let self=this;
//      获取参数
     let subjectId = self.$route.query.id;
      API_Subject.querySubjectById({subjectId:subjectId}).then(function (res) {
        self.subjectData=res;
        console.log(res);
      })
    }

  }
</script>

<style lang="less" rel="stylesheet/less">
  .topic{
    .head{
      .img_box{
        height: 350px;
        width: 750px;
        overflow: hidden;
        position: relative;
        img{
          width: 100%;
        }
        .title{
          position: absolute;
          font-size: 40px;
          height: 100px;
          line-height: 100px;
          width: 750px;
          top:50%;
          margin-top: -50px;
          text-align: center;
          letter-spacing: 2px;;
        }
      }
    }
    .sm_title{
      height: 77px;
      font-size: 36px;
      line-height: 50px;
      padding-left: 30px;
    }
    .sub_content{
      padding: 25px 40px;
      line-height: 37px;
      font-size: 22px;
      text-align: justify;
      text-indent: 2em;
    }
    ul{
      width: 677px;
      margin: 0 auto;
      li {
        box-sizing: border-box;
        width: 677px;
        height: 351px;
        padding: 40px 0;
        letter-spacing: 1px;
        border-bottom: 1px solid gray;
        a {
          display: flex;
          width: 677px;
          .imgbox {
            flex: none;
            width: 222px;
            height: 270px;
            img {
              width: 222px;
              height: 270px;
            }
            image[lazy=loading] {
              width: 222px;
              height: 270px;
            }
          }
          .left {
            flex: auto;
            padding-left: 35px;
            .title {
              white-space: nowrap;
              height: 90px;
              font-size: 36px;
              font-weight: 500;
              width: 300px;
              text-overflow: ellipsis;
              overflow: hidden;
              line-height: 40px;
            }
            .teacher {
              height: 57px;
              font-size: 28px;
              color: #2d2d2d;
            }
            .attention {
              height: 57px;
              font-size: 24px;
              color: #555555;
            }
            .price {
              font-size: 28px;
              color: #ecb76d;
            }

          }
          .sourcetype {
            /*flex: 1;*/
            display: inline-block;
            width: 60px;
            height: 40px;
            font-size: 24px;
            line-height: 40px;
            text-align: center;
            color: white;
            background-color: #ff875d;
          }
        }
      }
    }
  }
</style>



