<template>

  <div class="skill-box" :class='showAndFalse'>

    <span v-for='(item,index) in wordArr' >
    <router-link :to='"/classIfiCation/?skillInfoId="+item.id'>{{ item.name }}</router-link>
    </span>
    <span @click='showAll'>+</span>

  </div>

</template>

<script type="text/ecmascript-6">
  import API_lesson from '../../api/LessonMain';

  export default {
    data () {
      return {

        show:false
      };

    },
    created () {


    },
    props:['wordArr'],
    computed:{
        showAndFalse:function () {
          if(this.show) {
            return 'big-skill-box'
          }else {
            return 'sm-skill-box'
          }

        }
    },
    methods:{
      showAll:function () {
        this.show=!this.show;
      }
    }
  }
</script>

<style lang="less" rel="stylesheet/less">

  .skill-box {
    width: 100%;
    display: flex;
    overflow: hidden;
    flex-wrap:wrap;
    position: relative;
    transition:all 0.5s ease ;
    &.big-skill-box{
      height: auto;
    }
    &.sm-skill-box{
      height: 62px;
    }
    span {
      flex: none;
      height: 62px;
      font-size: .22*100px;
      line-height: .62*100px;
      width: 106px;
      text-align: center;
      &:last-child {
        position: absolute;
        right: 0px;
        order:2;
        font-size: 0.4*100px;
      }
    }
  }
</style>



