import http from './httputil';
const API_Subject = {

  queryRecommendedSubject: function (params) {
    let defUrl = '/subject/queryRecommendedSubject';
    let defParams = {
      reommend: 1
    };
    //合并参数
    let obj = Object.assign(defParams, params);

    return http.fetch(defUrl, obj)
  },
  querySubjectById: function (params) {
    let defUrl = '/subject/querySubjectById';
    let defParams = {};
    //合并参数
    let obj = Object.assign(defParams, params);

    return http.fetch(defUrl, obj)
  },
}


export default API_Subject;



