<template>
  <div class='popular'>
    <div class='title' v-show='title'>
      <span></span>
      &nbsp;&nbsp;{{ title }}&nbsp;&nbsp;
      <span></span>
    </div>
    <ul class='items-list' v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10"
        >

      <li v-for='(item, index) in topList'>
        <router-link :to='"/course/?id="+item[0].id'>
          <div class="imgbox">
            <img v-lazy="item[0].coverImg">
          </div>
          <div class="left">
            <div class="title">
              {{ item[0].title }}
            </div>
            <div class="teacher">
              讲师：{{ item[1].nickName }}
            </div>
            <div class="attention">
              {{ item[0].likeNum }}人关注&nbsp/&nbsp{{ item[0].vipNum }}人购买
            </div>
            <div class="price">
              <span>￥{{ item[0].price }}&nbsp/&nbsp年</span>
            </div>
          </div>
          <span class="sourcetype">
          在行
        </span>
        </router-link>
      </li>

    </ul>
  </div>
</template>

<script type='text/ecmascript-6'>
  import {mapState} from 'vuex';
  import {Lazyload} from 'mint-ui';
  //  无限下拉
  import {InfiniteScroll} from 'mint-ui';
  export default {
    data () {
      return {
        first: false,

      };
    },
    props: ['postData', 'postType', 'title'],

    computed: {
      topList: function () {
        let str = 'topList' + this.postType;
        return this.$store.state.popular[str];
      },

    },
    methods: {
      loadMore: function () {

        this.$store.dispatch('upDateTopList', {postData: this.postData, postType: this.postType});
      }

    },
    created: function () {

    },
    watch: {

    },
    beforeDestroy: function () {
      this.first = false;
    }
  }
</script>

<style lang='less' rel='stylesheet/less'>
  .popular {
    width: 6.77*100px;
    margin: 0 auto;
    padding-top: .3*100px;
    .title {
      height: .37*100px;
      font-size: .36*100px;
      display: flex;
      align-items: center;
      span {
        flex: 1;
        height: 0;
        border-top: 1px solid gray;
      }

    }
    .items-list {
      width: 677px;
      li {
        box-sizing: border-box;
        width: 677px;
        height: 351px;
        padding: 40px 0;
        letter-spacing: 1px;
        border-bottom: 1px solid gray;
        a {
          display: flex;
          width: 677px;
          .imgbox {
            flex: none;
            width: 222px;
            height: 270px;
            img {
              width: 222px;
              height: 270px;
            }
            image[lazy=loading] {
              width: 222px;
              height: 270px;
            }
          }
          .left {
            flex: auto;
            padding-left: 35px;
            .title {
              white-space: nowrap;
              height: 90px;
              font-size: 36px;
              font-weight: 500;
              width: 300px;
              text-overflow: ellipsis;
              overflow: hidden;
              line-height: 40px;
            }
            .teacher {
              height: 57px;
              font-size: 28px;
              color: #2d2d2d;
            }
            .attention {
              height: 57px;
              font-size: 24px;
              color: #555555;
            }
            .price {
              font-size: 28px;
              color: #ecb76d;
            }

          }
          .sourcetype {
            /*flex: 1;*/
            display: inline-block;
            width: 60px;
            height: 40px;
            font-size: 24px;
            line-height: 40px;
            text-align: center;
            color: white;
            background-color: #ff875d;
          }
        }
      }

    }
  }
</style>



