<template>
<div class="search">
    <div class="header">
      <input type="search" v-model='keyWord'  @keyup='onSubmit($event)'>
      <router-link to='/index1' class="btn" >取消</router-link>
    </div>
  <div class="tips" v-show='!show && firstSearch'>
    对不起,没有对应的课程,<br>换一个关键词词试试
  </div>
    <ul class="keyWord" v-show="!show">
      <li v-for="(item,index) in wordArr" @click='clickWord(item)'>
        {{item}}
      </li>
    </ul>

  <ul class='items-list' v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10" infinite-scroll-immediate-check='false'>

    <li v-for='(item, index) in dataList'>
      <router-link :to='"/course/?id="+item[0].id'>
        <div class="imgbox">
          <img :src="item[0].coverImg">
        </div>
        <div class="left">
          <div class="title">
            {{ item[0].title }}
          </div>
          <div class="teacher">
            讲师：{{ item[1].nickName }}
          </div>
          <div class="attention">
            {{ item[0].likeNum }}人关注&nbsp/&nbsp{{ item[0].vipNum }}人购买
          </div>
          <div class="price">
            <span>￥{{ item[0].price }}&nbsp/&nbsp年</span>
          </div>
        </div>
        <span class="sourcetype">
          在行
        </span>
      </router-link>
    </li>

  </ul>

</div>


</template>

<script type="text/ecmascript-6">
  import API_lesson from '../../api/LessonMain';
  import { Indicator } from 'mint-ui';
  import {InfiniteScroll} from 'mint-ui';
  export default {
    data () {
      return {
        wordArr:['职场','专业','心理','职场','专业','心理','职场','专业'],
        dataList:[],
        keyWord:'',
        showWord:true,
        firstSearch:false
      }
    },
    computed:{
        show () {
          let bool=this.dataList[0];
          console.log(bool);
          return bool;
        },
        parms (){
          let obj={key: this.keyWord,orderType:3,pageNumber:1}
          return obj;
        }
    },
    methods:{
      onSubmit (ev) {
          let self=this;
          console.log(self.parms);
        if(ev.keyCode==13){

          Indicator.open({
            text: '正在努力搜索~~~~',
            spinnerType: 'double-bounce'
          });
          API_lesson.queryLessonByKey(self.parms)
            .then(function (res) {
              self.dataList = res;
              self.firstSearch=true;
              console.log(res);
//          关闭加载中
              Indicator.close();

            }, function (err) {
              console.log(err)
            })


        }
      },
      loadMore: function () {

        let self=this;
        if(self.show){
          self.parms.pageNumber++;
          API_lesson.queryLessonByKey(self.parms)
            .then(function (res) {
              self.dataList.push(res);
              console.log(res);
//          关闭加载中
              Indicator.close();

            }, function (err) {
              console.log(err)
            })
        }
      },
      clickWord:function (value) {
        let self=this;
        self.keyWord=value;
        Indicator.open({
          text: '正在努力搜索~~~~',
          spinnerType: 'double-bounce'
        });
        API_lesson.queryLessonByKey(self.parms)
          .then(function (res) {
            self.firstSearch=true;
            self.dataList = res;
            console.log(res);
//          关闭加载中
            Indicator.close();
          }, function (err) {
            console.log(err)
          })
      }
    }
  }
</script>

<style lang="less" rel="stylesheet/less">
    .search{
      .header{
        position: relative;
        height: 130px;
        display: flex;
        align-items:center;
        justify-content:center;
        border: 1px solid gainsboro;
        input{
          flex: none;
          background-color: #f6f8f9;
          height: 60px;
          width: 674px;
          font-size: 23px;
          letter-spacing: 2px;
          padding-left: 4px;
        }
        .btn{
          position: absolute;
          font-size: 25px;
          text-align: center;
          width: 100px;
          height: 60px;
          line-height: 60px;
          right: 50px;
          top: 50%;
          margin-top: -30px;
          color: gray;
        }
      }
      .tips{
        height: 250px;
        padding: 70px 0;
        text-align: center;
        line-height: 50px;
        font-size: 28px;
        color: #e9c597;
        letter-spacing: 2px;
      }
      .keyWord{
        display: flex;
        width: 682px;
        margin: 0 auto;
        align-items:center;
        justify-content:center;
        flex-wrap: wrap;
        padding-top: 50px;
        li{
          flex: none;
          height: 60px;
          width: 120px;
          margin: 20px;
          text-align: center;
          font-size: 20px;
          line-height: 60px;
          border: 1px solid gray;
        }
      }
      .items-list {
        margin: 0 auto;
        width: 677px;
        li {
          box-sizing: border-box;
          width: 677px;
          height: 351px;
          padding: 40px 0;
          letter-spacing: 1px;
          border-bottom: 1px solid gray;
          a{
            display: flex;
            width: 677px;
            .imgbox {
              flex: none;
              width: 222px;
              height: 270px;
              img {
                width: 222px;
                height: 270px;
              }
            }
            .left {
              flex: auto;
              padding-left: 35px;
              .title {
                white-space: nowrap;
                height: 90px;
                font-size: 36px;
                font-weight: 500;
                width: 300px;
                text-overflow:ellipsis;
                overflow: hidden;
                line-height: 40px;
              }
              .teacher {
                height: 57px;
                font-size: 28px;
                color: #2d2d2d;
              }
              .attention {
                height: 57px;
                font-size: 24px;
                color: #555555;
              }
              .price {
                font-size: 28px;
                color: #ecb76d;
              }

            }
            .sourcetype {
              /*flex: 1;*/
              display: inline-block;
              width: 60px;
              height: 40px;
              font-size: 24px;
              line-height: 40px;
              text-align: center;
              color: white;
              background-color: #ff875d;
            }
          }
        }
      }
    }
</style>



