var OPERATION_SUCCESS = 0;
var OPERATION_FAILD = 1;
var WRONG_RAND_NUMBER = 2;
var DATA_EXIST = 3;
var NO_AUTHENTICATION = 5;
var WRONG_USER_NAME = 6;
var WRONG_USER_PASS = 7;
var INVALID_USER = 8;
var INVALID_DATA = 9;
var USERNAME_EXIST = 10;
var USER_EMAIL_EXIST = 11;

import axios from 'axios';

import qs from 'qs';

/* axios 配置 */
axios.defaults.timeout = 5000;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
axios.defaults.baseURL = 'http://192.168.202.16:8080/';
// axios.defaults.baseURL = 'http://api.111ji.com/';

/* POST传参序列化 */
axios.interceptors.request.use((config) => {
  if (config.method === 'post') {
    var ps = config.data;
    if (ps == null || ps === 'undefined')
      ps = {};

    if (window.localStorage && window.localStorage.getItem('api-access-token')) {
      config.headers = {'apiAccessToken': window.localStorage.getItem('api-access-token')};
      ps.apiAccessToken = window.localStorage.getItem('api-access-token');
    }
    if (window.localStorage && window.localStorage.getItem('user-access-token')) {
      config.headers = {'userAccessToken': window.localStorage.getItem('user-access-token')};
      ps.userAccessToken = window.localStorage.getItem('api-access-token');
    }
    ps.REQUEST_TYPE = 'api';
    config.data = qs.stringify(ps);
    config.params = ps;
  }
  return config;
}, (error) => {
  /*  _.toast('错误的传参', 'fail'); */
  return Promise.reject(error);
});

/* 返回状态判断 */
axios.interceptors.response.use((res) => {
  if (res.data.result !== 0) {
    /*  _.toast(res.data.msg); */
    return Promise.reject(res);
  }
  return res.data;
}, (error) => {
  /*  _.toast('网络异常', 'fail'); */
  return Promise.reject(error);
});

export default {
  fetch: function (url, params) {
    return new Promise((resolve, reject) => {

      axios.post(url, params)
        .then(function (response) {

          resolve(response.data);
        }, function (err) {
            reject(err);
        })
        .catch((error) => {
          reject(error);
        });
    });
  },
  /*
   get http request message with code
   @param resultCode code
   @return result message
   */

  getResultMessage: function (resultCode, lang) {
    if (!lang || lang != 'zh') {
      switch (resultCode) {
        case OPERATION_SUCCESS:
          return '操作成功';
        case OPERATION_FAILD:
          return '操作失败';
        case WRONG_RAND_NUMBER:
          return '验证码错误';
        case DATA_EXIST:
          return '输入错误或记录已存在';
        case NO_AUTHENTICATION:
          return '您没有权限进行该操作';
        case WRONG_USER_NAME:
          return '错误的用户名';
        case WRONG_USER_PASS:
          return '密码错误';
        case INVALID_USER:
          return '无效的用户(未激活或已注销)';
        case INVALID_DATA:
          return '输入错误或信息无效';
        case USERNAME_EXIST:
          return '昵称已存在';
        case USER_EMAIL_EXIST:
          return '邮箱已存在';
        default:
          return '处理失败，请检查您的操作';
      }
    } else {
      switch (resultCode) {
        case OPERATION_SUCCESS:
          return 'Success';
        case OPERATION_FAILD:
          return 'Failed';
        case WRONG_RAND_NUMBER:
          return 'Wrong verify code';
        case DATA_EXIST:
          return 'Data duplicate';
        case NO_AUTHENTICATION:
          return 'Authentication failed';
        case WRONG_USER_NAME:
          return 'Wrong user name';
        case WRONG_USER_PASS:
          return 'Wrong password';
        case INVALID_USER:
          return 'invalid user';
        case INVALID_DATA:
          return 'Invalid parameter[s]';
        case USERNAME_EXIST:
          return 'User exist';
        case USER_EMAIL_EXIST:
          return 'Email exist';
        default:
          return 'System failed';
      }
    }
  }
};






