// initial state
// shape: [{ id, quantity }]
//状态
const state = {
  postData: {
    orderType: 3,
    pageNumber: 0,
    pageSize: 15
  }
};

// getters
// 理解为  state的计算属性
const getters = {

};

// actions
//异步的mutations
const actions = {
  page_numadd:function (context) {
    context.commit('_PAGE_NUMADD')
  }
};

// mutations
//改变state状态的函数
const mutations = {
  _PAGE_NUMADD:function (state) {
    state.postData.pageNumber++;
  }

};

//暴露出去 store.js 整合
export default {
  state,
  getters,
  actions,
  mutations
}





