<template>
  <div class="courseDetails" v-if='isshow'>
    <div class="course_head">
      <div class="imgbox">
        <img :src="courseDetails.lesson.coverImg" class="coverimg">
      </div>
    </div>
    <div class="contain">
      <div class="info_box">
        <div class="left">
          <div class="title">
            {{ courseDetails.lesson.title }}
          </div>
          <div class="about">
            12课时&nbsp/&nbsp{{courseDetails.lesson.format}} &nbsp/&nbsp {{courseDetails.lesson.grade}}
          </div>
          <div class="attention">
            订阅人数:&nbsp{{ courseDetails.lesson.vipNum }}人
          </div>
          <div class="attention">
            订阅人数:&nbsp{{ courseDetails.lesson.likeNum }}人
          </div>
        </div>
        <div class="right">
          <div class="score">
            {{ courseDetails.lesson.score }}
          </div>
          <ul class="score_bottom">
            <li><span>实用性:</span>
              <star :size="48" :score='courseDetails.lesson.scoreCount'></star>
            </li>
            <li><span>实用性:</span>
              <star :size="48" :score='courseDetails.lesson.scoreForLesson'></star>
            </li>
            <li><span>实用性:</span>
              <star :size="48" :score='courseDetails.lesson.scoreForTeacher'></star>
            </li>
          </ul>
        </div>
      </div>
      <div class="teacherInfo">
        <div class="title">
          讲师
        </div>
        <ul>
          <li v-for='(item,index) in courseDetails.teacher'>
            <div class="headBox">
              <img :src="item[0].headImage" alt="">
              <span>{{ item[0].name }}</span>
            </div>
            <p>{{ item[0].description }}</p>
          </li>
        </ul>
      </div>
      <div class="item_box">
        <div class="title">适合人群</div>
        <p>{{ courseDetails.lesson.suitCrowds }}</p>
      </div>
      <div class="item_box">
        <div class="title">获益</div>
        <p>{{ courseDetails.lesson.benefit }}</p>
      </div>
      <div class="item_box">
        <div class="title">课程介绍</div>
        <p>{{ courseDetails.lesson.content }}</p>
      </div>
      <div class="item_box">
        <div class="title">购买须知</div>
        <p>{{courseDetails.lesson.buyInfo}}</p>
      </div>
      <div class="item_box">
        <div class="title">其他权益</div>
        <p>{{courseDetails.lesson.otherRights}}</p>
      </div>
      <div class="pingLun item_box">
        <div class="title">评论</div>
        <ul>
          <li v-for='(item,index) in pingLunData'>
            <div class="headBox">
              <img :src="item.headImage" alt="">
              <span>{{ item.userName }}</span>
            </div>
            <p>{{ item.content}}</p>
          </li>
        </ul>
      </div>
    </div>
    <div class="buybox" :show='courseDetails'>
      <div class="spice"> ¥ {{courseDetails.lesson.price}}</div>
      <div class="buy">去购买</div>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">
  import API_lesson from '../../../src/api/LessonMain';
  import {Indicator} from 'mint-ui';
  import star from '../star/star.vue'
  export default {
    data () {
      return {
        courseDetails: {},
        pingLunData: [],
        isshow: false
      }
    },
    computed: {
      selectedId: function () {
        return this.$store.state.popular.selectedId;
      }
    },
    created () {
//开启加载中
      Indicator.open({
        text: '加载中...',
        spinnerType: 'double-bounce'
      });
      let self = this;
      let id = self.$route.query.id;
//      this.selectedId

      API_lesson.queryLessonById({'id': id})
        .then(function (res) {
          self.courseDetails = res;
//          关闭加载中
          Indicator.close();
          self.isshow = true;
          API_lesson.queryLessonComment({'id': id}).then(
            function (res) {
              self.pingLunData = res;
              console.log(self.pingLunData)
            }
          )
        }, function (err) {
          console.log(err)
        })

    },
    components: {
      star
    }
  }
</script>

<style lang="less" rel="stylesheet/less">
  .courseDetails {
    width: 100%;
    .course_head {
      padding-bottom: 37px;
      .imgbox {
        text-align: center;
        margin: 0 auto;
        max-height: 380px;
        overflow: hidden;
        .coverimg {
          height: 380px;
          width: 100%;
        }
      }

    }
    .contain {
      padding: 0 35px;
      .info_box {
        display: flex;
        height: 200px;
        .left {
          flex: auto;
          .title {
            white-space: nowrap;
            height: 63px;
            font-size: 36px;
            font-weight: 500;
            width: 350px;
          }
          .about {
            height: 40px;
            font-size: 20px;
            color: #2d2d2d;
          }
          .attention {
            height: 40px;
            font-size: 20px;
            color: #555555;
          }
          .price {
            font-size: 28px;
            color: #ecb76d;
          }
        }
        .right {
          flex: none;
          width: 265px;
          height: 187px;
          .score {
            width: 65px;
            height: 65px;
            border-radius: 50%;
            line-height: 65px;
            margin: 0 auto;
            text-align: center;
            font-size: 24px;
            background-color: #ecb76d;
            color: white;
          }
          .score_bottom {
            height: 144px;
            padding-top: 5px;
            li {
              height: 38px;
              font-size: 22px;
              line-height: 38px;
              letter-spacing: 2px;
            }
          }
        }
      }
      .title {
        height: 50px;
        font-size: 28px;
        border-bottom: 1px solid gainsboro;
        padding-left: 5px;
        letter-spacing: 2px;
      }
      .teacherInfo {
        li {
          padding: 20px 0;
          .headBox {
            height: 72px;
            position: relative;
            img {
              width: 70px;
              height: 70px;
              border-radius: 50%;
              border: 1px solid gainsboro;
            }
            span {
              position: absolute;
              top: 22px;
              padding-left: 10px;
              font-size: 24px;
              letter-spacing: 2px;

            }
          }
          p {
            text-align: justify;
            padding-left: 60px;
            font-size: 22px;
            line-height: 30px;
            padding-bottom: 20px;
          }
        }
      }
      .item_box {
        p {
          text-align: justify;
          padding: 10px 0 10px 15px;
          letter-spacing: 1px;
          font-size: 23px;
          line-height: 32px;
          min-height: 100px;
        }
        &.pingLun {
          .headBox {
            height: 72px;
            position: relative;
            img {
              width: 70px;
              height: 70px;
              border-radius: 50%;
              border: 1px solid gainsboro;
            }
            span {
              position: absolute;
              top: 22px;
              padding-left: 40px;
              font-size: 24px;
              letter-spacing: 2px;
            }
          }
          p {

            padding: 10px 0 10px 20px;
            &::after {
              content: '';
              height: 10px;
              width: 10px;
              background-color: red;
            }
          }
        }

      }
    }
    .buybox {
      display: flex;
      height: 100px;
      line-height: 100px;
      text-align: center;
      font-size: 30px;
      letter-spacing: 2px;
      .spice {
        flex: auto;
        color: #e3b575;
        border: 1px solid gainsboro;
      }
      .buy {
        flex: none;
        width: 300px;
        color: white;

        background: #e3b575;
      }
    }

  }
</style>



